import React from "react";
import { graphql } from "gatsby";

import BlogHeader from "../components/blog/header";
import LatestPost from "../components/blog/latest-post";
import Layout from "../components/layout";
import Post from "../components/blog/post";
import PreviousPost from "../components/blog/previous-post";
import styles from "./styles/blog.module.scss";

const BlogPage = ({ data }) => {
	const latestPost = data.latestEntry.edges[0].node;
	const previousPost = data.previousEntry.edges[0].node;

	return (
		<Layout>
			<BlogHeader />
			<section className={styles.latest}>
				<LatestPost
					title={latestPost.frontmatter.title}
					image={
						latestPost.frontmatter.featuredImage.childImageSharp
							.fluid
					}
					date={latestPost.frontmatter.date}
					excerpt={latestPost.frontmatter.excerpt}
					to={`/blog${latestPost.fields.slug}`}
				/>
			</section>

			<section className={styles.previous}>
				<PreviousPost
					title={previousPost.frontmatter.title}
					image={
						previousPost.frontmatter.featuredImage.childImageSharp
							.fluid
					}
					date={previousPost.frontmatter.date}
					excerpt={previousPost.frontmatter.excerpt}
					to={`/blog${previousPost.fields.slug}`}
				/>
			</section>

			<div className={styles.posts}>
				{data.oldEntries.edges.map(({ node }) => (
					<Post
						key={node.id}
						title={node.frontmatter.title}
						image={
							node.frontmatter.featuredImage.childImageSharp.fluid
						}
						imgSizes={
							node.frontmatter.featuredImage.childImageSharp.sizes
						}
						date={node.frontmatter.date}
						to={`/blog${node.fields.slug}`}
					/>
				))}
			</div>
		</Layout>
	);
};

export default BlogPage;

export const query = graphql`
	query {
		latestEntry: allMarkdownRemark(
			sort: { fields: frontmatter___date, order: DESC }
			limit: 1
		) {
			edges {
				node {
					fields {
						slug
					}
					id
					frontmatter {
						title
						excerpt
						date(formatString: "MMMM DD, YYYY")
						featuredImage {
							childImageSharp {
								fluid(maxWidth: 1400) {
									...GatsbyImageSharpFluid
								}
							}
						}
					}
				}
			}
		}
		previousEntry: allMarkdownRemark(
			skip: 1
			sort: { fields: frontmatter___date, order: DESC }
			limit: 1
		) {
			edges {
				node {
					id
					fields {
						slug
					}
					frontmatter {
						date(formatString: "MMMM DD, YYYY")
						title
						excerpt
						featuredImage {
							childImageSharp {
								fluid(maxWidth: 800) {
									...GatsbyImageSharpFluid
								}
							}
						}
					}
				}
			}
		}
		oldEntries: allMarkdownRemark(
			skip: 2
			sort: { fields: frontmatter___date, order: DESC }
		) {
			edges {
				node {
					fields {
						slug
					}
					id
					frontmatter {
						date(formatString: "MMMM DD, YYYY")
						title
						featuredImage {
							childImageSharp {
								sizes {
									...GatsbyImageSharpSizes
								}
							}
						}
					}
				}
			}
		}
	}
`;
